
.history a {
  text-decoration: none !important;
}
.history ul {
  border-top: 1px solid #ccc;
}
.history .sns {
  margin-left: 10px;
  font-size: 13px;
  color: #999;
}
.history .sns i {
  font-style: normal;
}
.history .hot {
  color: #f06;
}
.history .date {
  padding: 12px 0;
  margin: 10px 0;
  margin-left: -59px;
  font-size: 0;
}
.history .date span {
  display: inline-block;
  padding: 2px 0;
  margin-right: 25px;
  width: 34px;
  height: 40px;
  background: #948cf1;
  color: #fff;
  text-align: center;
}
.history .date i {
  position: relative;
  font-style: normal;
  display: block;
}
.history .date i.m {
  top: 2px;
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 1px;
}
.history .date i.d {
  margin-top: 4px;
  font-size: 16px;
}
.history small {
  display: inline-block;
  height: 44px;
  line-height: 44px;
  color: #4e627a;
  text-align: center;
  font-size: 16px;
  font-weight: 600;
  vertical-align: top;
}


