
.history a {
  display: block;
}
.history li {
  width: 100%;
  padding: 8px 0;
  overflow: hidden;
  border-bottom: 1px solid #ccc;
}
.history li .title {
  display: inline-block;
  margin: 10px 0 0 10px;
  width: auto;
  max-width: 80%;
  font-size: 16px;
  line-height: 1.5;
}
.history li img {
  float: right;
  margin-right: 10px;
  width: 70px;
  height: 70px;
}
.btn-previous {
  position: relative;
  display: block;
  margin: 20px auto;
  height: 100px;
  width: 100px;
  border: 0;
  outline: none;
  color: #42b983;
  border-radius: 50%;
  background: #fff;
  cursor: pointer;
}
.btn-previous::before,
.btn-previous::after {
  position: absolute;
  left: 0;
  top: 0;
  height: 100px;
  width: 100px;
  border-radius: 50%;
  border-style: solid;
  border-width: 2px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  content: '';
}
.btn-previous::before {
  border-color: #c7c7c7;
}
.btn-previous::after {
  border-radius: 50%;
  border-style: solid;
  border-width: 4px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  content: '';
  left: 0;
  position: absolute;
  top: 0;
  -webkit-animation: MoreLoadingAnimation 2s infinite ease;
  -moz-animation: MoreLoadingAnimation 2s infinite ease;
  -o-animation: MoreLoadingAnimation 2s infinite ease;
  -ms-animation: MoreLoadingAnimation 2s infinite ease;
  animation: MoreLoadingAnimation 2s infinite ease;
  border-color: #42b983 rgba(0,0,0,0) rgba(0,0,0,0);
  -webkit-transform-origin: 50%;
  -moz-transform-origin: 50%;
  -o-transform-origin: 50%;
  -ms-transform-origin: 50%;
  transform-origin: 50%;
}
@media (max-width: 500px) {
.home li .title {
    font-size: 13px;
}
.home button {
    margin: 10px auto;
    height: 50px;
    width: 50px;
}
.home button span {
    display: none;
}
.home button::before,
  .home button::after {
    height: 50px;
    width: 50px;
    border-width: 2px;
}
.home button::after {
    border-width: 2px;
}
}
@-moz-keyframes MoreLoadingAnimation {
0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    transform: rotate(360deg);
}
}
@-webkit-keyframes MoreLoadingAnimation {
0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    transform: rotate(360deg);
}
}
@-o-keyframes MoreLoadingAnimation {
0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    transform: rotate(360deg);
}
}
@keyframes MoreLoadingAnimation {
0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    transform: rotate(360deg);
}
}


