
ul,
li {
  padding: 0;
  margin: 0;
}
body {
  margin: 0;
  padding: 0;
  height: 100%;
}
#forkme_banner {
  display: block;
  position: absolute;
  top: 0;
  right: 10px;
  z-index: 10;
  padding: 10px 40px 10px 10px;
  color: #fff;
  font-weight: 700;
  -webkit-box-shadow: 0 0 10px rgba(0,0,0,0.5);
  box-shadow: 0 0 10px rgba(0,0,0,0.5);
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
}
#forkme_banner span {
  margin-right: 5px;
}
#forkme_banner::after {
  content: '';
  position: absolute;
  width: 30px;
  height: 30px;
  background: url("./assets/github.png") no-repeat center center;
  -webkit-background-size: 100% 100%;
  -moz-background-size: 100% 100%;
  background-size: 100% 100%;
  top: 5px;
}
#app {
  display: -webkit-box;
  display: -moz-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: box;
  display: flex;
  -webkit-box-align: center;
  -moz-box-align: center;
  -o-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
  -webkit-box-pack: center;
  -moz-box-pack: center;
  -o-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  padding-top: 80px;
  color: #2c3e50;
  background-color: #fff;
  font-family: Lucida Grande, Helvetica Neue, Helvetica, Arial, Hiragino Sans GB, Hiragino Sans GB W3, WenQuanYi Micro Hei, Microsoft YaHei UI, Microsoft YaHei, sans-serif;
  font-size: 15px;
  background-color: #f2f3f5;
}
.view {
  padding: 0 25px;
  width: 750px;
  background: #fff;
}
.global-header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  height: 60px;
  background-color: #42b983;
  z-index: 1000;
}
.global-header .wrap {
  width: 800px;
  margin: auto;
  line-height: 60px;
}
.global-header img {
  display: inline-block;
  width: 126px;
  height: 42px;
  vertical-align: middle;
}
.global-header .power {
  float: right;
  margin: 0;
  color: #fff;
}
a {
  color: #42b983;
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: all 0.2s ease;
  -moz-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  -ms-transition: all 0.2s ease;
  transition: all 0.2s ease;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  filter: alpha(opacity=0);
}
@media (max-width: 500px) {
#forkme_banner {
    width: 30px;
    height: 30px;
    padding: 5px;
    background-position: center center;
    right: 0;
}
#forkme_banner span {
    display: none;
}
.global-header {
    height: 40px;
}
.global-header .wrap {
    width: 100%;
    line-height: 40px;
    margin-left: 10px;
}
.global-header .wrap img {
    width: 63px;
    height: 21px;
}
.global-header .wrap .power {
    display: none;
}
#app {
    padding-top: 40px;
}
.view {
    padding: 0 5px;
    width: 100%;
    overflow: hidden;
}
.view.detail {
    padding: 0 10px;
}
}


